.App {
  background-color: rgb(0, 24, 45);
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
  align-items: center;
  min-width: 325px;
}

.app_wrapper {
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
  background-color: #fff;
  padding: 0 18px;
  max-width: 375px;
  width: 100%;
  max-height: 800px;
  height: 100%;
}
