.Header {
  padding: 20px 0 0 0;
  position: sticky;
  top: 0;
  background-color: #fff;
  box-sizing: border-box;
  z-index: 60;
  width: 100%;
  left: 0;
}
.header_top_block {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.logo_block {
  display: flex;
}
.logo_icon_block {
  padding: 5px 9px 0 0;
}
.close {
  cursor: pointer;
}
